import React, {Component} from "react"

import Layout from "../components/layout";
import babygif from "../images/babyiam.gif";
import scrolldown from "../images/scrolldown.png";
import SEO from "../components/seo";
import Marquee from "../components/marquee";
import Footer from "../components/footer";
import { withMixpanel } from 'gatsby-plugin-mixpanel'

const imageSrcs = [
  'ikeamonkey.jpg', 
  'moira.png', 
  'littlehands.gif', 
  'boomer.jpg',
  'bll.png'
];

const instagrams = [
  'B5etyc3g1BT',
   'B5YTYG-JkRt',
   'B5l-jAGlbUW', 
   'B5lLZY-ADK0', 
   'B5j2imvFg2H', 
   'B5dmOb8KAQW', 
   'B5jdPzjpXxF', 
  'B5hCtZ2B-E6'];

class IndexPage extends Component {
  componentDidMount() {
    const { mixpanel } = this.props
    mixpanel.track('Page Load');
  }

  render() {
   return (
     <Layout>
      <SEO title="The Yoda Baby" />
      <div className="yoda-background">
        <img className="yoda-background__speech-bubble" src={babygif} alt="yoda saying'baby am i'" />
        <img className="yoda-background__speech-bubble--scroll" src={scrolldown} alt="yoda saying 'scroll down'" />
        <Marquee/>
      </div>
      <div className="meme-storage">
        <div className="memes">
          {imageSrcs.map(src =>
            <img src={`/memes/${src}`} alt="ikea monkey"/>
          )}
        </div>
        <div className="instagram">
          {instagrams.map(hash => <iframe
            src={`https://instagram.com/p/${hash}/embed`}
            frameborder="0"
            allowfullscreen
            scrolling="no"
            allowtransparency
            width="320"
            height="320"
            title="instagram post"
          ></iframe>)}
        </div>
        <div className="twitter">
        <a class="twitter-timeline" data-link-color="#19CF86" href="https://twitter.com/seymour_indiana/timelines/1201585958145183744?ref_src=twsrc%5Etfw">Baby Yoda - Curated tweets by seymour_indiana</a>
        </div>
        <Footer/>
      </div>
    </Layout>
   );
  }
}

export default withMixpanel()(IndexPage)
